<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>会员权益</p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                    <div class="other-content">
                        <div class="one">
                            <div class="title">游客</div>
                            <div class="des aa">浏览主页信息等通用信息，限制发布、咨询、参加平台自行
                                组织活动等互动事项</div>
                        </div>
                        <div class="one">
                            <div class="title dd">注册用户</div>
                            <div class="des bb">浏览主页信息等通用信息，限制发布、咨询、参加平台自行
                                组织活动等互动事项；可以进入个人中心界面</div>
                        </div>
                        <div class="one">
                            <div class="title">认证用户</div>
                            <div class="des cc">可以使用平台各项基础服务功能</div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </main>
</template>

<script>
    import { TabBar, Swiper, Icon, SwiperItem, ScrollView, ScrollViewMore } from 'mand-mobile'
    export default {
        name: 'News',
        components: {
            [TabBar.name]: TabBar,
            [Swiper.name]: Swiper,
            [SwiperItem.name]: SwiperItem,
            [ScrollView.name]: ScrollView,
            [Icon.name]: Icon,
            [ScrollViewMore.name]: ScrollViewMore
        },
        data () {
            return {
                // 当前选中tab
                current: 0,
                // tab项
                items: [{ name: 0, label: '国家政策' }, { name: 1, label: '军民指南' }],
                listLeft: 10,
                isFinished: false,
                Height: '900px',
                listRight: 10
            }
        },
        mounted () {
            console.log(window.innerHeight)
            this.Height = window.innerHeight - 84 + 'px'
        },
        methods: {
            // 回退
            goBack () {
                this.$router.push('/personal')
            },
            onSwiperChange (from, to) {
                this.current = to
            },
            onTabChange (item, index) {
                this.$refs.swiper.goto(index)
            },
            // 跳转新闻详情页
            $_onItemClick (i) {
                this.$router.push('/news/detail')
            },
            $_onEndReached () {
                if (this.isFinished) {
                    return
                }
                // async data
                setTimeout(() => {
                    this.listLeft += 10
                    if (this.listLeft >= 15) {
                        this.isFinished = true
                    }
                    this.$refs.scrollView.finishLoadMore()
                }, 1000)
            },
            $_onScroll ({ scrollLeft, scrollTop }) {
                // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
            },
            addItems () {
                this.list += 5
                // 如果把autoReflow设置为false, 需调用reflowScroller
                this.$refs.scrollView.reflowScroller()
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 88px
        width 100%
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .md-example-child-scroll-view-0
        height 600px
        padding-top 50px
        background #FFF
        .scroll-view-item
            width 90%
            margin  0 auto
            padding-bottom 40px
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
            border-bottom 2px solid #999
    .other-content
        width 90%
        margin 0 auto
        border-radius:12px;
        padding 50px 30px
        background:rgba(255,255,255,1);
        box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
        .one
            margin-bottom 30px
            .title
                text-align left
                text-indent 40px
                background url("../../../assets/personal/member benefits_icon_red@2x.png") no-repeat 10px 10px
                background-size 16px 16px
                font-size 28px
                color #666
            .des
                text-align left
                padding 0 20px
                font-size 28px
                margin-top 20px
                line-height 76px
                border-radius:8px
                color #333
            .aa
                background url("../../../assets/personal/member benefits_bg1@2x.png") no-repeat
                background-size 100% 100%
            .bb
                background url("../../../assets/personal/member benefits_bg2@2x.png") no-repeat
                background-size 100% 100%
            .cc
                height 144px
                line-height 144px
                background url("../../../assets/personal/member benefits_bg3@2x.png") no-repeat
                background-size 100% 100%
            .dd
                background url("../../../assets/personal/member benefits_icon_blue@2x.png") no-repeat 10px 10px
                background-size 16px 16px
</style>
